.header{
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 15.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__action{
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: 1em;
  }
}

.header-index{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: 3em 0;
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 7.125em;
    }
    &__btn{
      padding: 0 1em;
      height: 2.375em;
      margin-left: .5em;
    }
  }

  .header-index{
    padding: 2.25em 0;
  }
}


